import { Component, OnInit } from '@angular/core';
import { NzModalRef, NzMessageService } from 'ng-zorro-antd';
import * as Mock from 'mockjs';

@Component({
  selector: 'app-option-form',
  templateUrl: './option-form.component.html',
  styleUrls: ['./option-form.component.less']
})
export class OptionFormComponent implements OnInit {

  typeOptions = [];
  option = {};
  treeObj = {};
  isNum = true;
  sortSelections = [];
  dataList = [];
  gridStyle = {
    width: '25%',
    textAlign: 'center'
  };
  // 是否能有默认值
  isDefault = false;
  isImage = false;

  constructor(
    private modalRef: NzModalRef,
    private messageService: NzMessageService
  ) { }

  ngOnInit() {

    this.typeOptions = this.buildOptions();

    this.sortSelections = [
      { label: '升序', value: 'asc' },
      { label: '降序', value: 'desc' },
      // { label: '递增', value: 'increment' },
      // { label: '递减', value: 'decrement' }
    ];

    this.isNum = this.checkNum(this.option['type']);

    this.isImage = this.checkImage(this.option['type']);

    this.isDefault = this.checkImage(this.option['type']);

    this.initOption();
  }

  initOption() {
    if (!this.option['id']) {
      // 图片初始化
      this.option['img'] = {
        isRandom: 'y',
        width: 200,
        height: 200,
        text: 'This is my house !',
        color: '#FF6600'
      };

      // 数字初始化
      this.option['num'] = {
        min: 0,
        max: 100
      };
    }
  }

  buildOptions() {

    let options = [];

    let basicType = { label: '基本', value: 'basic', children: [] };
    let dateType = { label: '日期', value: 'date', children: [] };
    let imageType = { label: '图片', value: '@image', isLeaf: true };
    let textType = { label: '文本', value: 'text', children: [] };
    let nameType = { label: '姓名', value: 'name', children: [] };
    let webType = { label: '网络', value: 'web', children: [] };
    let addressType = { label: '地址', value: 'address', children: [] };


    let basicIdOption = { label: 'guid', value: '@guid', isLeaf: true };
    let basicStringOption = { label: '字符串', value: '@string', isLeaf: true };
    let basicBooleanOption = { label: '布尔', value: '@boolean', isLeaf: true };
    let basicNaturalOption = { label: '自然数', value: '@natural', isLeaf: true };
    let basicIntegerOption = { label: '整数', value: '@integer', isLeaf: true };
    let basicFloatOption = { label: '浮点数', value: '@float', isLeaf: true };
    let basicNumberOption = { label: '数字', value: 'number', children: [] };
    let basicPhoneOption = { label: '手机号码', value: 'phone', isLeaf: true };

    basicNumberOption.children.push(basicIntegerOption);
    basicNumberOption.children.push(basicNaturalOption);
    basicNumberOption.children.push(basicFloatOption);

    basicType.children.push(basicIdOption);
    basicType.children.push(basicNumberOption);
    basicType.children.push(basicBooleanOption);
    basicType.children.push(basicStringOption);
    basicType.children.push(basicPhoneOption);

    let dateOption = { label: '日期', value: 'date', children: [] };
    let dateFormatOption1 = { label: 'yyyy-MM-dd', value: '@datetime("yyyy-MM-dd")', isLeaf: true };
    let dateFormatOption2 = { label: 'yyyy-MM-dd HH:mm:ss', value: '@datetime("yyyy-MM-dd HH:mm:ss")', isLeaf: true };
    let dateFormatOption3 = { label: 'yyyyMMddHHmmss', value: '@datetime("yyyyMMddHHmmss")', isLeaf: true };

    dateOption.children.push(dateFormatOption1);
    dateOption.children.push(dateFormatOption2);
    dateOption.children.push(dateFormatOption3);

    let timeOption = { label: '时间', value: 'time', children: [] };
    let timeFormatOption1 = { label: 'HH:mm:ss', value: '@time("HH:mm:ss")', isLeaf: true };
    let timeFormatOption2 = { label: 'H:m:s', value: '@time("H:m:s")', isLeaf: true };

    timeOption.children.push(timeFormatOption1);
    timeOption.children.push(timeFormatOption2);

    let nowOption = { label: '当前', value: 'now', children: [] };
    let nowTime = { label: '时刻', value: '@now("second")', isLeaf: true };

    nowOption.children.push(nowTime);

    dateType.children.push(nowOption);
    dateType.children.push(dateOption);
    dateType.children.push(timeOption);

    let enParagraph = { label: '英文段落', value: '@paragraph', isLeaf: true };
    let chParagraph = { label: '中文段落', value: '@cparagraph', isLeaf: true };
    let enSentence = { label: '英文句子', value: '@sentence', isLeaf: true };
    let chSentence = { label: '中文句子', value: '@csentence', isLeaf: true };
    let enTitle = { label: '英文标题', value: '@title', isLeaf: true };
    let chTitle = { label: '中文标题', value: '@ctitle', isLeaf: true };

    let textTitleOption = { label: '标题', value: 'title', children: [] };
    let textParagraphOption = { label: '段落', value: 'paragraph', children: [] };
    let textSentenceOption = { label: '句子', value: 'sentence', children: [] };

    textTitleOption.children.push(enTitle);
    textTitleOption.children.push(chTitle);
    textParagraphOption.children.push(enParagraph);
    textParagraphOption.children.push(chParagraph);
    textSentenceOption.children.push(enSentence);
    textSentenceOption.children.push(chSentence);

    textType.children.push(textTitleOption);
    textType.children.push(textParagraphOption);
    textType.children.push(textSentenceOption);


    let enName = { label: '英文名', value: '@name', isLeaf: true };
    let chName = { label: '中文名', value: '@cname', isLeaf: true };

    nameType.children.push(enName);
    nameType.children.push(chName);

    let webUrlOption = { label: 'URL', value: '@url("http")', isLeaf: true };
    let webDomainOption = { label: '域名', value: '@domain', isLeaf: true };
    let webEmailOption = { label: '邮箱', value: '@email', isLeaf: true };
    let webIpOption = { label: 'IP', value: '@ip', isLeaf: true };

    webType.children.push(webUrlOption);
    webType.children.push(webDomainOption);
    webType.children.push(webEmailOption);
    webType.children.push(webIpOption);

    let addressRegionOption = { label: '地区', value: '@region', isLeaf: true };
    let addressProvinceOption = { label: '省', value: '@province', isLeaf: true };
    let addressCityOption = { label: '市', value: 'city', children: [] };
    let addressCountyOption = { label: '县', value: 'county', children: [] };
    let addressZipOption = { label: '邮编', value: '@zip', isLeaf: true };

    let cityOption1 = { label: '带省', value: '@city(true)', isLeaf: true };
    let cityOption2 = { label: '不带省', value: '@city', isLeaf: true };
    addressCityOption.children.push(cityOption1);
    addressCityOption.children.push(cityOption2);

    let countyOption1 = { label: '带省市', value: '@county(true)', isLeaf: true };
    let countyOption2 = { label: '不带省市', value: '@county', isLeaf: true };
    addressCountyOption.children.push(countyOption1);
    addressCountyOption.children.push(countyOption2);

    addressType.children.push(addressRegionOption);
    addressType.children.push(addressProvinceOption);
    addressType.children.push(addressCityOption);
    addressType.children.push(addressCountyOption);
    addressType.children.push(addressZipOption);

    options.push(basicType);
    options.push(dateType);
    options.push(textType);
    options.push(nameType);
    options.push(webType);
    options.push(addressType);
    options.push(imageType);

    return options;
  }

  typeChanges(result) {
    this.isNum = this.checkNum(result);
    this.isDefault = this.checkDefault(result);
    this.isImage = this.checkImage(result);

  }

  checkNum(arr) {
    return (arr && arr[1] && arr[1] == 'number');
  }

  checkDefault(arr) {
    return (arr && arr[0] && arr[0] == '@image');
  }

  checkImage(arr) {
    return (arr && arr[0] && arr[0] == '@image');
  }

  submitForm() {

    if (!this.validateForm()) {
      return;
    }

    let data = {
      title: this.option['title'],
      key: this.option['title'],
      isLeaf: true,
      option: this.option
    };

    if (this.option['id']) {
      // 编辑
      this.closeMoal(data);

    } else {
      // 添加
      if (!this.existPropName(data)) {
        data.option['id'] = Mock.mock('@guid');
        this.dataList.push(data)
        this.option = {};
        this.initOption();
        this.treeObj[data.title] = data;
      } else {
        this.messageService.error('属性名已存在')
      }
    }
  }


  // 校验表单
  validateForm() {
    if (!this.option['title']) {
      this.messageService.error('属性名不能为空')
      return false;
    }

    if (!this.validVarible(this.option['title'])) {
      this.messageService.error('属性名只能是字母(a-z A-Z)，数字(0-9)，下划线(_)，美元符号($)的组合，并且之间不能包含空格，数字不能放在属性名首位')
      return false;
    }

    if (!this.option['type']) {
      this.messageService.error('类型能为空');
      return false;
    }

    if (this.isNum) {
      let minNum = this.option['num']['min'];
      let maxNum = this.option['num']['max'];
      if (maxNum <= minNum) {
        this.messageService.error('最大值不能小于等于最小值');
        return false;
      }
    }

    return true;
  }

  // 属性名是否合法
  validVarible(varible: string) {
    let reg = /^[$_a-zA-Z][$_a-zA-Z0-9]*$/
    return reg.test(varible);
  }

  existPropName(node) {
    if (this.treeObj.hasOwnProperty(node.title)) {
      return true;
    }

    return false;
  }


  closeMoal(data) {
    this.modalRef.destroy(data);
  }

  close() {
    if (this.option['id']) {
      this.closeMoal(null);
    } else {
      this.closeMoal(this.dataList);
    }
  }

  // 是否随机颜色下拉框变化
  randomColorChange(value) {
    this.option['img']['isRandom'] = value;
  }

}
