import { Component, OnInit } from '@angular/core';
import { DataService } from '../../service/data.service';
import { NzMessageService, NzModalRef } from 'ng-zorro-antd';
import * as ClipboardJS from 'clipboard';

@Component({
  selector: 'app-data-table',
  templateUrl: './data-table.component.html',
  styleUrls: ['./data-table.component.less']
})
export class DataTableComponent implements OnInit {

  dataSet = [];
  options = [];
  count = 10;
  copiedSql = '';
  copiedJson = '';
  sqlClipBoard;
  jsonClipBoard;
  tableName = '';

  constructor(
    private dataService: DataService,
    private messageService: NzMessageService,
    private modalRef: NzModalRef
  ) {
  }

  ngOnInit() {
    this.loadData();

    // 复制到剪切版
    this.sqlClipBoard = new ClipboardJS('.copyAsSql');
    this.sqlClipBoard.on('success', (e) => {
      this.messageService.success('已复制至剪切板');
    });

    this.jsonClipBoard = new ClipboardJS('.copyAsJson');
    this.jsonClipBoard.on('success', (e) => {
      this.messageService.success('已复制至剪切板');
    });

  }

  ngOnDestroy() {
    this.sqlClipBoard.destroy();
    this.jsonClipBoard.destroy();
  }

  loadData() {

    this.dataSet = this.dataService.generate(this.options, this.count);
    this.copiedSql = this.dataService.generateAsSQL(this.dataSet);

  }

  refresh() {
    this.loadData();
  }

  copyAs(type = 'sql') {
    if (type === 'sql') {
      this.tableName = this.tableName === '' ? 'demo' : this.tableName;
      let reg = /demo/g;
      this.copiedSql = this.copiedSql.replace(reg, this.tableName);
    } else {
      this.copiedJson = JSON.stringify(this.dataSet);
    }
  }

}
