import { Injectable } from '@angular/core';
import * as Mock from 'mockjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor() { }

  // 生成数据
  generate(options, count = 10) {
    let result = [];
    if (options.length > 0) {

      options.forEach(option => {
        let prop = option['title'];
        let mockDataArr = this.mockMultiData(option, count);
        mockDataArr = this.dealData(option, mockDataArr);
        this.putData(prop, result, mockDataArr);
      });

    }

    return result;
  }

  // 处理生成数据
  private dealData(option, dataArr) {
    let sort = option['sort'];
    if (sort === 'asc') {
      dataArr = dataArr.sort((a, b) => {
        return a - b;
      });
    } else if (sort === 'desc') {
      dataArr = dataArr.sort((a, b) => {
        return b - a;
      });
    }
    return dataArr;
  }

  private putData(prop, result, data) {
    data.forEach((item, index) => {
      let dataItem = result[index];
      if (!dataItem) {
        dataItem = {};
      }
      dataItem[prop] = item;
      result[index] = dataItem;
    })
  }


  private mockMultiData(option, count) {
    let dataArr = [];
    for (let i = 1; i <= count; i++) {
      let data = this.getDataByOption(option, count);
      dataArr.push(data);
    }
    return dataArr;
  }

  // 通过 option 判断，生成数据
  private getDataByOption(option, count) {
    let data;
    let typeLen = option.type.length;
    let type = option.type[typeLen - 1];
    if (type == '@image') {
      // 图片类型

      let image = option.img;
      if (!image.width) {
        image.width = '200'
      }

      if (!image.height) {
        image.height = '200'
      }

      if (!image.text) {
        image.height = 'This is my house !'
      }

      if (image.isRandom == 'y') {
        image.color = Mock.mock('@color');
      }

      let template = `${type}('${image.width}x${image.height}', ${image.color}, ${image.text})`;
      data = Mock.mock(template);

    } else if ((type == '@natural') || (type == '@integer') || (type == '@float')) {
      // 数据类型
      let num = option['num'];
      let min = num['min'];
      let max = num['max'];
      // 存在范围
      if (min !== '' && max !== '') {
        let template = `${type}(${min}, ${max})`;
        data = Mock.mock(template);

      } else {
        data = Mock.mock(type);
      }

    } else if (type == 'phone') {
      // 手机类型
      let phoneReg = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
      let template = { 'regexp': phoneReg };
      data = Mock.mock(template)['regexp'];
      let defaultValue = option['defaultValue'];
      if (phoneReg.test(defaultValue)) {
        data = defaultValue;
      }
    } else {
      // 其他类型
      if (option['defaultValue'] && (type == '@domain' || type == '@email')) {
        // 如果有默认值，且类型为 域名或邮箱
        let template = `${type}(${option['defaultValue']})`;
        data = Mock.mock(template);
      } else if (option['defaultValue']) {
        // 有默认值，其他类型
        type = { 'array|1': option['defaultValue'].split(',') };
        data = Mock.mock(type)['array'];
      } else {
        // 没有默认值
        data = Mock.mock(type);
      }

    }

    return data;
  }

  // 生成为 sql 
  generateAsSQL(dataList, tableName = 'demo') {
    // insert into table value
    let colStr = this.getColumnStr(dataList[0]);
    let resultSql = '';
    dataList.forEach(item => {
      let insertSql = `INSERT INTO ${tableName} ( ${colStr} ) VALUES  ( `;
      Object.values(item).forEach(value => {
        if (typeof value === 'number') {
          insertSql += `${value},`;
        } else {
          insertSql += `"${value}",`;
        }
      });
      // 把多余的 , 删除
      insertSql = insertSql.substring(0, insertSql.length - 1);
      insertSql += ` );`
      resultSql += insertSql + '\n'
    });

    return resultSql;
  }

  // 获取列字符串
  private getColumnStr(record) {
    let colStr = Object.keys(record).join(',');
    return colStr;
  }

  // 解析 sql create 
  getNodesBySql(sql: string) {
    // 去除 . [ ] 
    let reg = /[\.\[\]]/g;
    sql = sql.replace(reg, '');
    let leftBracketsIndex = sql.indexOf('(');
    let rightBracketsIndex = sql.lastIndexOf(')');
    // 通过 , 分离每一列
    let columns = sql.substring(leftBracketsIndex + 1, rightBracketsIndex).split(',');
    let separateReg = /[\s(]/g;
    let nodes = [];
    columns.forEach(column => {
      let columnType = column.trim().split(separateReg);
      let defaultIndex = columnType.findIndex(e => {
        return e.toLowerCase() == 'default';
      });
      let columnInfo = { prop: columnType[0], type: columnType[1], default: '' };
      if (defaultIndex >= 0) {
        columnInfo['default'] = columnType[defaultIndex + 1].replace(/['"]/g, '');
      }
      let node = this.getNode(columnInfo);
      nodes.push(node);
    });

    return nodes;
  }

  // 通过 列 信息生成节点
  private getNode(column) {

    let option = {
      id: Mock.mock('@guid'),
      title: column['prop'],
      defaultValue: column['default']
    };
    option['num'] = { min: 0, max: 100 };
    option['img'] = {
      isRandom: 'y',
      width: 200,
      height: 200,
      text: 'This is my house !',
      color: '#FF6600'
    };

    let dataType = column['type'].toLowerCase();

    if (dataType.indexOf('char') >= 0) {
      // 字符串
      option['type'] = ['basic', '@string'];
    } else if (dataType.indexOf('text') >= 0) {
      // 文本
      option['type'] = ['text', 'paragraph', '@cparagraph'];
    } else if (dataType.indexOf('int') >= 0) {
      // int
      option['type'] = ['basic', 'number', '@integer'];
    } else if ((dataType.indexOf('float') >= 0) || (dataType.indexOf('double') >= 0) || (dataType.indexOf('decimal') >= 0) || (dataType.indexOf('numeric') >= 0)) {
      // float, double, decimal, numeric
      option['type'] = ['basic', 'number', '@float'];
    } else if (dataType.indexOf('bool') >= 0) {
      // bool
      option['type'] = ['basic', '@boolean'];
    } else if (dataType === 'date') {
      // 日期 yyyy-MM-dd
      option['type'] = ['date', 'date', '@datetime("yyyy-MM-dd")'];
    } else if (dataType === 'datetime') {
      // 日期 yyyy-MM-dd HH:mm:ss
      option['type'] = ['date', 'date', '@datetime("yyyy-MM-dd HH:mm:ss")'];
    } else if (dataType.indexOf('time') >= 0) {
      // 时间
      option['type'] = ['date', 'time', '@time("HH:mm:ss")'];
    } else if (dataType.indexOf('image') >= 0) {
      // 图片
      option['type'] = ['@image'];
    } else {
      // 其他类型暂处理为字符串
      option['type'] = ['basic', '@string'];
    }

    let node = {
      title: option['title'],
      key: option['title'],
      isLeaf: true,
      option: option
    };
    return node;
  }
}
