import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { NgZorroAntdModule, NZ_I18N, zh_CN } from 'ng-zorro-antd';
import { registerLocaleData } from '@angular/common';
import zh from '@angular/common/locales/zh';
import { AppRoutingModule } from './app-routing/app-routing.module';
import { DataTreeComponent } from './components/data-tree/data-tree.component';
import { HomeComponent } from './components/home/home.component';
import { OptionFormComponent } from './components/option-form/option-form.component';
import { DataTableComponent } from './components/data-table/data-table.component';
import { SqlInputComponent } from './components/sql-input/sql-input.component';


registerLocaleData(zh);

@NgModule({
  declarations: [
    AppComponent,
    DataTreeComponent,
    HomeComponent,
    OptionFormComponent,
    DataTableComponent,
    SqlInputComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule, ReactiveFormsModule,
    HttpClientModule,
    NgZorroAntdModule,
    AppRoutingModule
  ],
  providers: [{ provide: NZ_I18N, useValue: zh_CN }],
  bootstrap: [AppComponent]
})
export class AppModule { }
