import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { DataTreeComponent } from '../components/data-tree/data-tree.component';
import { HomeComponent } from '../components/home/home.component';
import { OptionFormComponent } from '../components/option-form/option-form.component';
import { DataTableComponent } from '../components/data-table/data-table.component';
import { SqlInputComponent } from '../components/sql-input/sql-input.component';

const appRoutes: Routes = [
  {
    path: 'tree',
    component: DataTreeComponent
  },
  {
    path: 'index',
    component: HomeComponent
  },
  {
    path: '',
    pathMatch: 'full',
    redirectTo: '/index'
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes)
  ],
  declarations: [],
  exports: [
    RouterModule
  ],
  entryComponents: [OptionFormComponent, DataTableComponent, SqlInputComponent]
})
export class AppRoutingModule { }
