import { OnInit, Component, ViewChild } from '@angular/core';
import { NzFormatEmitEvent, NzTreeComponent, NzTreeNode, NzMessageService, NzModalRef, NzModalService } from 'ng-zorro-antd';
import { OptionFormComponent } from '../option-form/option-form.component';
import { DataTableComponent } from '../data-table/data-table.component';
import { SqlInputComponent } from '../sql-input/sql-input.component';
import { DataService } from '../../service/data.service';

@Component({
  selector: 'app-data-tree',
  templateUrl: './data-tree.component.html',
  styleUrls: ['./data-tree.component.less']
})
export class DataTreeComponent implements OnInit {


  @ViewChild('treeCom') treeCom: NzTreeComponent;
  activedNode: NzTreeNode;
  nodes = [];
  dataCount = 10;
  checkedKeys = [];

  constructor(
    private dataService: DataService,
    private messageService: NzMessageService,
    private modalService: NzModalService
  ) {
  }

  ngOnInit() {
    this.nodes = [];
  }

  activeNode(data: NzFormatEmitEvent): void {
    this.activedNode = data.node;
  }

  nzEvent(event: NzFormatEmitEvent): void {
  }

  // 选中节点
  checkNode(event: NzFormatEmitEvent) {
    this.checkedKeys = event.keys;
  }

  // 添加节点
  addNode() {

    const modal = this.modalService.create({
      nzTitle: '添加',
      nzContent: OptionFormComponent,
      nzFooter: null,
      nzClosable: false,
      nzMaskClosable: false,
      nzWidth: 800,
      nzComponentParams: {
        treeObj: this.getTreeObj()
      }
    });

    modal.afterClose.subscribe((dataList) => {
      if (dataList && dataList.length > 0) {
        this.nodes = [...this.nodes].concat(dataList);
      }
    });

  }

  // 节点 是否存在
  existNode(nodes: any[], key: string) {
    let exist = false;
    if (nodes) {
      nodes.forEach(node => {
        if (node.key == key) {
          exist = true;
        }
      });
    }
    return exist;
  }

  // 编辑节点
  editNode() {

    if (!this.activedNode) {
      this.messageService.error("请选择");
      return;
    }

    const modal = this.modalService.create({
      nzTitle: '编辑',
      nzContent: OptionFormComponent,
      nzFooter: null,
      nzClosable: false,
      nzWidth: 800,
      nzComponentParams: {
        option: this.activedNode.origin.option,
        treeObj: this.getTreeObj()
      }
    });

    modal.afterClose.subscribe((node) => {
      if (!node) {
        return;
      }

      let nodeArr = [];
      this.treeCom.getTreeNodes().forEach(treeNode => {
        if (treeNode.origin['option']['id'] == node['option']['id']) {
          nodeArr.push(node);
        } else {
          nodeArr.push(treeNode.origin);
        }
      });

      this.nodes = nodeArr;
    });
  }

  // 删除节点
  deleteNode() {
    if (this.checkedKeys.length == 0) {
      this.messageService.error("请选择");
      return;
    }

    this.nodes = this.nodes.filter(node => {
      return this.checkedKeys.indexOf(node.key) < 0;
    });

  }

  // 全选
  selectAll() {
    this.checkedKeys = this.getKeys();
  }

  // 反选
  unselect() {

    /**
     * 
     * @TODO
     * 全选后，不能反选
     * 
     */

    let keys = this.getKeys();
    this.checkedKeys = keys.filter(key => {
      return this.checkedKeys.indexOf(key) < 0;
    });
  }

  // 获取所有 key
  getKeys() {
    let keys = [];
    if (this.nodes) {
      this.nodes.forEach(node => {
        keys.push(node.key);
      });
    }
    return keys;
  }

  // 获取树对象
  getTreeObj() {
    let obj = {};
    if (this.treeCom && this.treeCom.getTreeNodes()) {
      this.treeCom.getTreeNodes().forEach(node => {
        let key = node.origin.key;

        if (!!!obj[key]) {
          obj[key] = node.origin;
        }

      });
    }
    return obj;
  }

  existPropName(node) {
    let treeObj = this.getTreeObj();
    if (treeObj.hasOwnProperty(node.title)) {
      return true;
    }

    return false;
  }

  getOptionArr() {
    let optionArr = [];
    if (this.treeCom && this.treeCom.getTreeNodes()) {
      this.treeCom.getTreeNodes().forEach(node => {
        let option = node.origin['option'];
        optionArr.push(option)
      });
    }

    return optionArr;
  }

  generateData() {
    let options = this.getOptionArr();

    if (options.length > 0) {
      const modal = this.modalService.create({
        nzTitle: '共 ' + this.dataCount + ' 条数据',
        nzContent: DataTableComponent,
        nzFooter: null,
        nzMaskClosable: false,
        nzWidth: '90%',
        nzComponentParams: {
          options: options,
          count: this.dataCount
        }
      });
    }
  }

  // 打开 输入sql 模态框
  inputSql() {
    const modal = this.modalService.create({
      nzTitle: '输入SQL CREATE 语句',
      nzContent: SqlInputComponent,
      nzFooter: null,
      nzMaskClosable: false,
      nzWidth: 800,
      nzComponentParams: {
      }
    });

    modal.afterClose.subscribe((sql) => {
      if (sql) {
        let nodes = this.dataService.getNodesBySql(sql);
        this.nodes = nodes;
      }
    });
  }

}
